.exam-tips {
  padding-top: 80px;
  width: 100%;
  height: 100vh;
  background: linear-gradient(180deg, #f8fbfc 0, #f1fafd 100%);
  box-sizing: border-box;
}

.exam-tips-content {
  margin: 50px auto 60px auto;
  padding: 50px 20px;
  width: 600px;
  background-color: #fff;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 10px;
}

.exam-tips-content > p {
  margin-bottom: 30px;
  font-size: 32px;
  line-height: 50px;
  color: #333;
}

.exam-tips-btn {
  margin: 0 auto;
  width: 640px;
  height: 90px;
  background: linear-gradient(90deg, #26c3fd 0, #3f8eff 100%);
  border-radius: 45px;
  font-size: 30px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.exam-tips-ant-btn {
  margin: 0 auto;
  width: 640px;
  height: 90px;
}
